<template>
    <div class="qr-code under-construction">
        <div id="bcid"></div>
        <div style="position: fixed;bottom: 0;left: 0;right: 0;z-index: 99999;">
        	<mu-button @click="setFlash" full-width color="primary" style="border-radius: 0;height: 55px;" >{{flashText}}</mu-button>
        </div>
    </div>
</template>

<script>
let app = null;
let scan = null;
export default {
  name: "QRCode",
  data() {
    return {
      qr: "",
      flashText:'开启闪光灯'
    };
  },
  created() {
    app = this;
  },
  mounted() {
    if (!window.plus) return;
    app.qrInit();
  },
  computed:{
  	
  },
  beforeDestroy() {
  	if(scan)
    scan.close();
  },
  methods: {
    qrInit() {
    	let _this = this;
	    scan = new plus.barcode.Barcode(
	        "bcid",
	        [plus.barcode.QR, plus.barcode.EAN8, plus.barcode.EAN13],
	        { frameColor: "#00FF00", scanbarColor: "#00FF00" }
	    );
	    scan.onmarked = onmarked;
        //开始扫描
        scan.start();
	    function onmarked(type, result, file) {
		    let codeUrl = result.replace(/\n/g, '');;
		    _this.$store.commit('setQrCodeResult' , codeUrl)
		    _this.$router.replace('/transferAccounts')
	    }
    },
    setFlash() {
    	if(this.flashText=='开启闪光灯'){
			scan.setFlash('true');
			this.flashText='关闭闪光灯'
    	}else{
    		scan.setFlash('false');
			this.flashText='开启闪光灯'
    	}

	}
  }
};
</script>
<style scoped>
#bcid {
  width: 100%;
  position: absolute;
  bottom: 55px;
  left: 0px;
  right: 0;
  top: 50px;
  text-align: center;
  background-color: #000000;
}
</style>




